import { Block, Text } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';
import { ContainerData, Container } from '@ali/4ever-plugin-container';

export interface ColorBlocksData extends ContainerData<'colorBlocks'> {
  readonly metadata: {
    /**
     * 表情
     */
    sticker?: string;
    /**
     * 是否显示表情
     */
    showstk?: boolean;
    /**
     * 默认字体色
     */
    color?: string;
    /**
     * 边框色
     */
    border?: string;
    /**
     * 背景色
     */
    bgcolor?: string;
  };
}

export type ColorBlocksMetaData = ColorBlocksData['metadata'];

export class ColorBlocks extends Container<ColorBlocksData> {
  static SUB_TYPE = ELEMENT_TYPE;

  static isColorBlocks(node: any): node is ColorBlocks {
    return Container.isContainer(node, ColorBlocks.SUB_TYPE);
  }

  static createColorBlocks(
    metadata: Partial<ColorBlocksMetaData> = {},
    nodes?: Block[],
  ) {
    return Container.createContainer({
      data: {
        subType: ColorBlocks.SUB_TYPE,
        metadata,
      },
      nodes: nodes || [
        Block.create({
          type: 'paragraph',
          nodes: [Text.create()],
        }),
      ],
    }) as ColorBlocks;
  }
}
